<template>
    <div class='Ask'>
        <div class="header">
            <div>
                <img :src="car.details && car.details.serial.CoverPhoto | pic_URL" alt="">
            </div>
            <div>
                <p>{{car.details && car.details.serial.AliasName}}</p>
                <p>{{car.details && car.details.car_name}}</p>
            </div>
        </div>
        <div class="body">
            <p>个人信息</p>
            <div class='user_name'>
                <input type="text" placeholder="请输入您的真实姓名">
                <span>姓名</span>
            </div>
            <div class='user_number'>
                <input type="number" placeholder="请输入您的真实手机号">
                <span>手机号</span>
            </div>
            <div class='city'>
                <input readonly type="text" :value='$route.query.city.CityName'>
                <img src="../../assets/back.png" alt="">
                <span>城市</span>
            </div>
            <div>
                <button>询问最低价</button>
            </div>
        </div>
        <div class="bottom">
            <p>选择报价经销商</p>
            <ul>
                <li v-for="(it,i) in car.list" :key='i'>
                    <div class='radio'>
                        <input type="radio" name="a" id="">
                    </div>
                    <div class='right'>
                        <div>
                            <p>{{it.dealerShortName}}</p>
                            <p>{{it.address}}</p>
                        </div>
                        <div>
                            <p>{{it.discount_info}}</p>
                            <p>售{{it.saleRange}}</p>
                        </div>
                    </div>
                </li>
            </ul>
        </div>
        <div class='bottom_button'>
            <button>
                <p>询问最低价</p>
            </button>
        </div>
    </div>
</template>
<script>
export default {
    name:'Ask',
    data(){
        return {
            car:''
        }
    },
    created(){
        if(!this.$route.query.city.CityID){
            this.$router.back();//可以改用vuex
        }
        this.axios.get('/car_api/v2-dealer-alllist.html?carId='+
        this.$route.query.carId + "&cityId=" + this.$route.query.city.CityID).
        then(res => {
             if(res.status ===200 && res.data.code === 1){
                 console.log(res.data.data);
                 this.car = res.data.data;
             }
        });
    }
}
</script>
<style lang='stylus' scoped>
    .Ask
        position absolute
        height 100%
        width 100%
        top 0
        z-index 32
        background-color white
        .header
            height 120px
            display flex
            padding 10px 10px
            align-items center
            div:first-child
                height 80px
                border-radius px
                box-shadow 0 0 8px #aaa
                overflow hidden
                border-radius 10px
                img
                    height 100%
                    border-radius 10px
            div:last-child
                display flex
                justify-content space-around
                flex-direction column
                height 80px
                margin-left 16px
                p:first-child
                    font-size 18px
                    font-weight 700
                p:last-child
                    font-size 14px
        .body
            position relative
            display flex
            flex-direction column
            p
                font-size 14px
                padding 10px 12px
                background-color #eeeeee
            input
                border 0
                border-bottom 1px solid #aaa
                height 40px
                text-align right
                width 100%
            div
                width 100%
                position relative
                input
                    padding-left 30px
