                    padding 0 50px
                    margin 0 auto
                    font-size 12px
                    background-color #5babf8
                    border 0
                    border-radius 4px
                    color white
        .bottom
            >p
                font-size 14px
                padding 10px 12px
                background-color #eeeeee
            ul
                height 100%
                overflow auto
                li
                    display flex
                    align-items center
                    .radio
                        width 20px
                    .right
                        height 100%
                        width 100%
                        padding 14px 6px
                        display flex
                        justify-content space-between
                        p:first-child
                            font-size 16px
                            margin-bottom 16px
                            font-weight 600
                        p:last-child
                            font-size 12px
                            color #aaa
                        div:last-child p
                            font-size 12px
                            text-align right
                            &:first-child
                                font-size 14px
                                color #ea372d
        .bottom_button
            position absolute
            bottom 0
            width 100%
            height 50px
            button 
                width 100%
                height 100%
                background-color #5babf8
                color white
                border 0
                p
                    font-size 16px
                    &:last-child
                        font-size 12px
                        margin-top 2px
</style>